#exper {
    margin: 0 auto;
    max-height: calc(180vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}

.experTitle {
    margin: 1rem 0;
    font-size: 3rem;
}

.experDesc {
    font-weight: 300;
    font-size: 1rem;
    max-width: 45rem;
}

.experImgs {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;
}

.experImg {
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
}

.experBtn {
    margin: 3rem 0;
    padding: 0.5rem 2.5rem;
    border: none;
    border-radius: 2rem;
    background: white;
    font-size: 1rem;
}

@media screen and (max-width:585px) {
    .experImg {
        height: 48vw;
    }
}